<template>
  <div id="app">
    <div class="content">
      <header class="header">
          <h1>OMP macro</h1>
      </header>
      <div class="content-main">
        <div class="top-btn-container">
          <button class="btn legend-btn" data-toggle="tooltip" data-placement="bottom" title="Legend" @click="openCloseLegend"></button>
          <button class="btn print-btn" data-toggle="tooltip" data-placement="bottom" title="Print to PDF" @click="openClosePartsToPrint"></button>
        </div>
        <div class="padding small">
          <p>
            This tool allows to get the MASTER sheet from the original OMP.
            <br/><br/>
            In order to use it, it has to be executed on the file with the OMP sheet.
            <br/><br/>
            Select the checkbox only if the aircraft model is 330, and then, run the macro by pressing the "GET MASTER" button.
          </p>
          <div class="btn-container">
            <button v-if="debugg" class="btn btn-warning"  @click="test2">Test</button>
            <button v-if="debugg" class="btn btn-warning"  @click="printTransformedOMP">PRINT Transformed OMP</button>
            <div class="checkbox-container">
              <input type="checkbox" id="optimized" v-model="optimized">
              <label for="optimized">A330 model</label>
            </div>
            <button class="btn btn-secondary"  @click="generateMaster(optimized)">GET MASTER</button>
            <!-- <button class="btn btn-primary"  @click="generateMaster(true)">GET MASTER (FH & FC optimized)</button> -->
            <button v-if="debugg" class="btn btn-danger" @click="deleteSheet">Remove sheet</button>
          </div>
        </div>
      </div>
    </div>
    <div class="popup-background" :class="[popupOpened ? '' : 'hidden']">
        <div class="loading-container" :class="[loading ? '' : 'hidden']">
            <span>Loading...</span>
        </div>

        <div class="legend-container" :class="[legend ? '' : 'hidden']">
          <header class="legend-header">
            <h3>
              Legend
            </h3>
            <button @click="openCloseLegend">
              X
            </button>
          </header>
          <div class="legend-item">
            <div class="color-container color-khaki"></div>
            <span class="color-description">
              MODIFY AND REMOVE BACKGROUND
            </span>
          </div>
          <div class="legend-item">
            <div class="color-container color-yellow"></div>
            <span class="color-description">
              MODIFY
            </span>
          </div>
          <div class="legend-item">
            <div class="color-container color-pink"></div>
            <span class="color-description">
              SELECT OPTION
            </span>
          </div>
          <div class="legend-item">
            <div class="color-container color-palegreen"></div>
            <span class="color-description">
              FILL
            </span>
          </div>
          <div class="legend-item">
            <div class="color-container color-skyblue"></div>
            <span class="color-description">
              INFORMATION
            </span>
          </div>
          <div class="legend-item">
            <div class="color-container color-black"></div>
            <span class="color-description">
              DO NOT TOUCH
            </span>
          </div>
        </div>

        <div class="error-container" :class="[error ? '' : 'hidden']">
          <span></span>
        </div>

        <div class="parts-to-print-container" :class="[partsToPrint ? '' : 'hidden']">
          <div>
            <h5>Select the elements to be printed</h5>
            <button class="btn btn-primary parts-to-print-close-btn" @click="openClosePartsToPrint">X</button>
          </div>
          <div>
            <div class="input-container">
              <input type="radio" id="airframe-radio" name="element-to-print" value="Airframe" v-model="elementToPrint">
              <label for="airframe-radio">Airframe</label>
            </div>
            <div class="input-container">
              <input type="radio" id="apu-radio" name="element-to-print" value="APU" v-model="elementToPrint">
              <label for="apu-radio">APU</label>
            </div>
            <div class="input-container">
              <input type="radio" id="engine1-radio" name="element-to-print" value="Engine 1" v-model="elementToPrint">
              <label for="engine1-radio">Engine 1</label>
            </div>
            <div class="input-container">
              <input type="radio" id="engine2-radio" name="element-to-print" value="Engine 2" v-model="elementToPrint">
              <label for="engine2-radio">Engine 2</label>
            </div>
            <div class="input-container">
              <input type="radio" id="engine3-radio" name="element-to-print" value="Engine 3" v-model="elementToPrint">
              <label for="engine3-radio">Engine 3</label>
            </div>
            <div class="input-container">
              <input type="radio" id="engine4-radio" name="element-to-print" value="Engine 4" v-model="elementToPrint">
              <label for="engine4-radio">Engine 4</label>
            </div>
          </div>
          <div class="parts-to-print-btn-container">
            <div>
              <button class="btn print-to-pdf-btn" :class="[filtered ? filtered === elementToPrint ? 'btn-success' : 'btn-danger' : 'btn-secondary']" @click="filterExcelSheets('Transformed OMP', elementToPrint)">FILTER</button>
              <button class="btn btn-secondary print-to-pdf-btn" :class="[filtered ? '' : 'disabled']" @click="unfilterExcelSheets('Transformed OMP')">UNFILTER</button>
            </div>
            <button class="btn btn-secondary print-to-pdf-btn" :class="[filtered ? '' : 'disabled']" @click="printToPDF('Transformed OMP')">PRINT</button>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import {
  createNewSheet,
  addRemainingHeaderRows,
  stylingNewTableHeaderRow,
  changeFrozenHeaderRows,
  insertRemainingColumns,
  insertHeaderNames,
  addValuesThresholdIntervalColumns,
  setUpRightSideParameters,
  addFormulasLastDoneNextDues,
  activateAutoFilter,
  setColumnsWidth,
  setHeaderRowsHeight,
  styleMergingCells,
  setBackgroundColor,
  buildTableGrids,
  setPrintConfigurations,
  styleLastDoneNextDueColumns,
  highlighteNoteRows,
  hideUnnecessaryColumns,
  addEndOfReport,
  addSignSquare
} from "@/js/functionsCreateMaster";

import {
  getSliceAsync,
} from "@/js/functionsPrintPdf";

  export default {
    name: 'App',
    data() {
      return {
        // CHANGE THIS VALUE BEFORE BUILD
        debugg: false,
        // debugg: true,

        // Interface params
        popupOpened: false,
        loading: false,
        legend: false,
        error: false,
        partsToPrint: false,
        fileContent: "",

        // Logic params
        optimized: false,
        elementToPrint: "Airframe",
        filtered: false,

        // Data
        airbusReferences: {
          "Airframe": "FM0800800",
          "Engine 1": "FM2300881 - 1.0",
          "Engine 2": "FM2300881 - 1.0",
          "Engine 3": "FM2300881 - 1.0",
          "Engine 4": "FM2300881 - 1.0",
          "APU": "FM2300879 - 1.0"
        },
      }
    },
    computed: {
      allPrintedPartsEmpty() {
        return this.airframe || this.apu || this.engine1 || this.engine2 || this.engine3 || this.engine4;
      },
      dictionaryPartsToPrint() {
        return {
          Airframe: this.airframe,
          APU: this.apu,
          "Engine 1": this.engine1,
          "Engine 2": this.engine2,
          "Engine 3": this.engine3,
          "Engine 4": this.engine4,
        }
      }
    },
    methods: {
      // Get the column name from the number (0 indexed)
      getColumnNameFromNumber(n) {
        const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");
        let result;
        if(n >= letters.length) {
          result = letters[Math.floor(n/letters.length)-1] + letters[n%letters.length];
        } else {
          result = letters[n];
        }
        return result;
      },

      openCloseLegend() {
        if(this.popupOpened === true && this.legend === true) {
          this.popupOpened = false;
          this.legend = false;
        } else if(this.popupOpened === false && this.legend === false) {
          this.popupOpened = true;
          this.legend = true;
        } else {
          console.log("Error");
        }
      },

      openClosePartsToPrint() {
        if(this.popupOpened === true && this.partsToPrint === true) {
          this.popupOpened = false;
          this.partsToPrint = false;
        } else if(this.popupOpened === false && this.partsToPrint === false) {
          this.elementToPrint = "Airframe";
          this.filtered = false;
          this.popupOpened = true;
          this.partsToPrint = true;
        } else {
          console.log("Error");
        }
      },

      generateMaster(optimized=false) {
        window.Excel.run(async context => {
          const self = this;

          self.popupOpened = !self.popupOpened;
          self.loading = !self.loading;
          try {
            const newSheet = await createNewSheet(context);
            if(newSheet !== false) {
              await addRemainingHeaderRows(newSheet);
              await stylingNewTableHeaderRow(context, newSheet, self);
              await changeFrozenHeaderRows(newSheet);
              await insertRemainingColumns(context, newSheet, optimized, self);
              await insertHeaderNames(context, newSheet, optimized, self);
              await addValuesThresholdIntervalColumns(context, newSheet, optimized, self);
              await setUpRightSideParameters(context, newSheet, self);
              await addFormulasLastDoneNextDues(context, newSheet, optimized, self);
              await styleLastDoneNextDueColumns(context, newSheet, optimized, self);
              await activateAutoFilter(newSheet);
              await setColumnsWidth(context, newSheet, optimized);
              await setHeaderRowsHeight(newSheet, optimized);
              await styleMergingCells(context, newSheet, optimized, self);
              await setBackgroundColor(context, newSheet, self);
              await highlighteNoteRows(context, newSheet, optimized, self);
              await hideUnnecessaryColumns(context, newSheet, optimized, self);
              await buildTableGrids(context, newSheet, optimized, self);
              await setPrintConfigurations(context, newSheet, self, self.debugg);
              await addEndOfReport(context, newSheet, self);
              await addSignSquare(context, newSheet, self);
            } else {
              console.log("The excel sheet already exists!");
            }
          } catch(e) {
            console.log(e);
            // Error message
          }
          self.popupOpened = !self.popupOpened;
          self.loading = !self.loading;
        });
      },

      deleteSheet() {
        window.Excel.run(async context => {
          const myWorkbook = context.workbook;
          const originalSheet = myWorkbook.worksheets.getItem("Transformed OMP");
          originalSheet.delete();
        });
      },

      test() {
        window.Excel.run(async context => {
          const sheet = context.workbook.worksheets.getActiveWorksheet()
          const usedRange = sheet.getUsedRange();
          usedRange.load("values");
          usedRange.load("rowCount");
          await context.sync();

          const remarksColumnNumber = usedRange.values[5].indexOf("Remarks:");
          const remarksColumnName = this.getColumnNameFromNumber(remarksColumnNumber);

          sheet.pageLayout.setPrintArea(`A7:${remarksColumnName}${usedRange.rowCount}`);
          sheet.pageLayout.setPrintTitleRows("$1:$6");
          sheet.pageLayout.centerHorizontally = true;
          sheet.pageLayout.orientation = "Landscape";
          sheet.pageLayout.zoom = { horizontalFitToPages: 1 };
        });
      },

      hideShowExcelSheets(sheetName, hide=true) {
        window.Excel.run(async context => {
          // Create a copy from the original MPD/OMP sheet
          const myWorkbook = context.workbook;
          myWorkbook.load();
          myWorkbook.load("worksheets");
          await context.sync();
          myWorkbook.worksheets.items.forEach(async ws => {
            const wsName = ws.name;
            if(wsName != sheetName) {
              ws = myWorkbook.worksheets.getItem(wsName);
              ws.visibility = hide ? window.Excel.SheetVisibility.hidden : window.Excel.SheetVisibility.visible;
            }
          });
        });
      },

      filterExcelSheets(sheetName, criteria) {
        window.Excel.run(async context => {
          // Create a copy from the original MPD/OMP sheet
          const myWorkbook = context.workbook;
          const sheet = myWorkbook.worksheets.getItem(sheetName);
          const usedRange = sheet.getUsedRange();
          usedRange.load({$all: true});
          await context.sync();

          const remarksColumnNumber = usedRange.values[5].indexOf("Remarks:");
          sheet.getRange(`${this.getColumnNameFromNumber(remarksColumnNumber+8)}1:${this.getColumnNameFromNumber(remarksColumnNumber+8)}1`).values = [[criteria]];
          const filterRange = sheet.getRange(`A6:${this.getColumnNameFromNumber(usedRange.columnCount)}${usedRange.rowCount}`);
          sheet.autoFilter.apply(filterRange, remarksColumnNumber+13, {
            criterion1: `=*${criteria}*`,
            filterOn: "Custom"
          });

          const reference = this.airbusReferences[criteria];
          const headerFooter = sheet.pageLayout.headersFooters.defaultForAllPages;
          headerFooter.load({$all: true}); // Load header/footer
          await context.sync();
          headerFooter.set({leftFooter: `OMP (${reference})`});

          this.filtered = this.elementToPrint;
        });
      },

      unfilterExcelSheets(sheetName) {
        window.Excel.run(async context => {
          // Create a copy from the original MPD/OMP sheet
          const myWorkbook = context.workbook;
          const sheet = myWorkbook.worksheets.getItem(sheetName);
          const usedRange = sheet.getUsedRange();
          usedRange.load({$all: true});
          await context.sync();

          const remarksColumnNumber = usedRange.values[5].indexOf("Remarks:");
          sheet.getRange(`${this.getColumnNameFromNumber(remarksColumnNumber+8)}1:${this.getColumnNameFromNumber(remarksColumnNumber+8)}1`).values = [[""]];
          sheet.autoFilter.clearColumnCriteria(remarksColumnNumber+13);

          const headerFooter = sheet.pageLayout.headersFooters.defaultForAllPages;
          headerFooter.load({$all: true}); // Load header/footer
          await context.sync();
          headerFooter.set({leftFooter: `OMP`});

          this.filtered = false;
        });
      },

      async printToPDF(sheetName) {
        const delay = ms => new Promise(res => setTimeout(res, ms));
        this.hideShowExcelSheets(sheetName);
        await delay(2000);
        window.Office.context.document.getFileAsync(window.Office.FileType.Pdf, { sliceSize: 65536 /*64 KB*/ },
          function(result) {
            if (result.status == "succeeded") {
              var myFile = result.value;
              var sliceCount = myFile.sliceCount;
              var slicesReceived = 0, gotAllSlices = true, docdataSlices = [];
              // console.log("File size:" + myFile.size + " #Slices: " + sliceCount);

              // Get the file slices.
              getSliceAsync(myFile, 0, sliceCount, gotAllSlices, docdataSlices, slicesReceived);
              // myFile.closeAsync();
            }
            else {
              console.log("Error:", result.error.message);
            }
          }
        );
        this.hideShowExcelSheets(sheetName, false);          
      },

      async printSelectedPartsToPDF() {
        this.partsToPrint = false;
        this.loading = true;
        const delay = ms => new Promise(res => setTimeout(res, ms));
        Object.keys(this.dictionaryPartsToPrint).forEach(async k => {
            if(this.dictionaryPartsToPrint[k]) {
              this.hideShowExcelSheets();
              this.filterExcelSheets("Transformed OMP", k);
              await delay(1000);
              window.Office.context.document.getFileAsync(window.Office.FileType.Pdf, { sliceSize: 65536 /*64 KB*/ },
                function(result) {
                  if (result.status == "succeeded") {
                    var myFile = result.value;
                    var sliceCount = myFile.sliceCount;
                    var slicesReceived = 0, gotAllSlices = true, docdataSlices = [];
                    // console.log("File size:" + myFile.size + " #Slices: " + sliceCount);
  
                    // Get the file slices.
                    getSliceAsync(myFile, 0, sliceCount, gotAllSlices, docdataSlices, slicesReceived);
                    // myFile.closeAsync();
                  }
                  else {
                    console.log("Error:", result.error.message);
                  }
                }
              );
              await delay(1000);
              this.hideShowExcelSheets(false);
            }
        });
        await delay(10000);
        this.loading = false;
        this.popupOpened = false;
      },
      
      async printSelectedPartToPDF() {
        this.partsToPrint = false;
        this.loading = true;
        const delay = ms => new Promise(res => setTimeout(res, ms));
        Object.keys(this.dictionaryPartsToPrint).forEach(async k => {
            if(this.dictionaryPartsToPrint[k]) {
              this.hideShowExcelSheets();
              this.filterExcelSheets("Transformed OMP", k);
              await delay(1000);
              window.Office.context.document.getFileAsync(window.Office.FileType.Pdf, { sliceSize: 65536 /*64 KB*/ },
                function(result) {
                  if (result.status == "succeeded") {
                    var myFile = result.value;
                    var sliceCount = myFile.sliceCount;
                    var slicesReceived = 0, gotAllSlices = true, docdataSlices = [];
                    // console.log("File size:" + myFile.size + " #Slices: " + sliceCount);
  
                    // Get the file slices.
                    getSliceAsync(myFile, 0, sliceCount, gotAllSlices, docdataSlices, slicesReceived);
                    // myFile.closeAsync();
                  }
                  else {
                    console.log("Error:", result.error.message);
                  }
                }
              );
              await delay(1000);
              this.hideShowExcelSheets(false);
            }
        });
        await delay(10000);
        this.loading = false;
        this.popupOpened = false;
      },

      async test2() {
        window.Excel.run(async context => {
          // Create a copy from the original MPD/OMP sheet
          const myWorkbook = context.workbook;
          const sheet = myWorkbook.worksheets.getItem("Transformed OMP");
          const usedRange = sheet.getUsedRange();
          usedRange.load("values");
          usedRange.load("rowCount");
          await context.sync();

          const remarksColNumber = usedRange.values[[5]].indexOf("Remarks:");
          const nextRemarksColName = this.getColumnNameFromNumber(remarksColNumber+1);
          console.log(`${nextRemarksColName}${usedRange.rowCount+1}`);
          let cell = sheet.getRange(`${nextRemarksColName}${usedRange.rowCount+1}`)
          cell.load('top,left') //pre-load top and left
          let myBase64 = this.getBase64();
          const shape = sheet.shapes.addImage(myBase64)

          await context.sync()

          shape.height = 179;
          shape.width = 235;
          shape.incrementLeft(cell.left - 237.5) // <- left
          shape.incrementTop(cell.top) // <-top
          await context.sync();
        });
      },
      getBase64() {
        return "";
      }
    }
  };
</script>

