// Create a copy from the original MPD/OMP sheet
const createNewSheet = async function(context) {
    const myWorkbook = context.workbook;
    const originalSheet = myWorkbook.worksheets.getItem("OMP");
    const newSheet = originalSheet.copy("Beginning");
    newSheet.name = "Transformed OMP";
    try {
      await context.sync();
    } catch {
      return false;
    }
    newSheet.activate();
    newSheet.position = 0;
    return newSheet;
}

// Insert the rows needed to have 5 as the page header
const addRemainingHeaderRows = async function(newSheet) {
    const rangeToMoveDown = newSheet.getRange("$3:$5");
    rangeToMoveDown.insert("Down");
}

// Formating the new row that will be the table header
const stylingNewTableHeaderRow = async function(context, newSheet, self) {
    const usedRange = newSheet.getUsedRange();
    usedRange.load("values");
    usedRange.load("columnCount");
    await context.sync();
    const lastHeaderColumnName = self.getColumnNameFromNumber(usedRange.columnCount-1);
    const headerRowRange = newSheet.getRange(`A6:${lastHeaderColumnName}6`);
    headerRowRange.format.rowHeight = 54;
    headerRowRange.format.fill.color = "lightgrey";
}

// Setting the freezing row according to the movement done
const changeFrozenHeaderRows = async function(newSheet) {
    newSheet.freezePanes.unfreeze();
    newSheet.freezePanes.freezeRows(6);
}

 // Insert remaining columns for the thresholds and intervals
const insertRemainingColumns = async function(context, newSheet, optimized=false, self) {
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");
    const usedRange = newSheet.getUsedRange();
    usedRange.load("values");
    usedRange.load("columnCount");
    usedRange.load("rowCount");
    await context.sync();
    const sampleThresholdColumnNumber = usedRange.values[5].indexOf("SAMPLE\nTHRESHOLD");
    const sampleThresholdColumnName = self.getColumnNameFromNumber(sampleThresholdColumnNumber);
    const originalLastColumn = usedRange.getColumn(usedRange.columnCount-1);
    originalLastColumn.insert("Right");
    originalLastColumn.load("values");
    await context.sync();
    const finalLastColumn = usedRange.getColumn(usedRange.columnCount-1);
    finalLastColumn.values = originalLastColumn.values;
    originalLastColumn.getCell(5,0).values = [["Remarks:"]];
    for(let i=6; i<usedRange.rowCount; i++) {
        originalLastColumn.getCell(i,0).values = [[""]];
    }

    const qttOfThresholdAndInterval = optimized ? 12 : 4;
    const lastIntervalColumn = optimized ? 11 : 3;
    const nextDueColumns = optimized ? 20 : 14;
    for(let i=0; i<qttOfThresholdAndInterval; i++) {
        const index = letters.indexOf(sampleThresholdColumnName) + i*5;
        for(let j=1; j<(i === lastIntervalColumn ? nextDueColumns : 5 ); j++) {
            const range = newSheet.getUsedRange().getColumn(index+j);
            range.insert("Right");
        }
    }
}

// Insert header names for the columns added
const insertHeaderNames = async function(context, newSheet, optimized=false, self) {
    // Adding new names on main header
    const header = newSheet.getUsedRange().getRow(5);
    header.load("values");
    await context.sync();
    const headerValues = header.values[0].map(x => x);
    const newHeaderValues = [
        [
            ...headerValues.slice(0,headerValues.indexOf("SAMPLE\nTHRESHOLD")+1),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("SAMPLE\nINTERVAL"), headerValues.indexOf("SAMPLE\nINTERVAL")+1),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("100%\nTHRESHOLD"), headerValues.indexOf("100%\nTHRESHOLD")+1),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("100%\nINTERVAL"), headerValues.indexOf("100%\nINTERVAL")+1),
            "CAL various", "CAL in days", "FH", "FC",
            ...(optimized ? 
            // FH optimized values
            [...headerValues.slice(headerValues.indexOf("SAMPLE\nTHRESHOLD")+20,headerValues.indexOf("SAMPLE\nTHRESHOLD")+1+20),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("SAMPLE\nINTERVAL")+20, headerValues.indexOf("SAMPLE\nINTERVAL")+1+20),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("100%\nTHRESHOLD")+20, headerValues.indexOf("100%\nTHRESHOLD")+1+20),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("100%\nINTERVAL")+20, headerValues.indexOf("100%\nINTERVAL")+1+20),
            "CAL various", "CAL in days", "FH", "FC",
            // FC optimized values
            ...headerValues.slice(headerValues.indexOf("SAMPLE\nTHRESHOLD")+20+20,headerValues.indexOf("SAMPLE\nTHRESHOLD")+1+20+20),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("SAMPLE\nINTERVAL")+20+20, headerValues.indexOf("SAMPLE\nINTERVAL")+1+20+20),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("100%\nTHRESHOLD")+20+20, headerValues.indexOf("100%\nTHRESHOLD")+1+20+20),
            "CAL various", "CAL in days", "FH", "FC",
            ...headerValues.slice(headerValues.indexOf("100%\nINTERVAL")+20+20, headerValues.indexOf("100%\nINTERVAL")+1+20+20),
            "CAL various", "CAL in days", "FH", "FC",] :
            []),
            "Date", "FH", "FC",
            "Date", "FH", "FC",
            "Date", "FH", "FC",
            ...(optimized ? 
            ["Date", "FH", "FC",
            "Date", "FH", "FC"] :
            []),
            ...headerValues.slice(headerValues.indexOf("SOURCE"))
        ]
    ];
    header.values = newHeaderValues;
    
    // Adding the name gruop on the subheader
    const firstColumnLastDue = headerValues.indexOf("100%\nINTERVAL") + (optimized ? 45 : 5);
    const subHeaderNames = [
        "LAST DONE",
        ...(optimized ? 
        ["NEXT DUE FH optimized not sampled",
        "NEXT DUE FH optimized sampled",
        "NEXT DUE FC optimized not sampled",
        "NEXT DUE FC optimized sampled"] :
        ["NEXT DUE not sampled",
        "NEXT DUE sampled",])
    ]
    const qttSubHeaders = optimized ? 5 : 3;
    for(let i=0; i<qttSubHeaders; i++){
        let firstColumn = self.getColumnNameFromNumber(firstColumnLastDue + i*3);
        let lastColumn = self.getColumnNameFromNumber(firstColumnLastDue + 2 + i*3);
        newSheet.getRange(firstColumn+"5").values = [[subHeaderNames[i]]];
        newSheet.getRange(`${firstColumn}5:${lastColumn}5`).merge(true);
    }
    if(optimized) {
        const STcolumnNumber = headerValues.indexOf("SAMPLE\nTHRESHOLD");
        
        newSheet.getRange(`${self.getColumnNameFromNumber(STcolumnNumber)}5`).values = [["NON-RANGE SENSITIVE"]];
        const nonRangeSensitiveRange = newSheet.getRange(`${self.getColumnNameFromNumber(STcolumnNumber)}5:${self.getColumnNameFromNumber(STcolumnNumber+19)}5`);
        nonRangeSensitiveRange.merge();
        
        newSheet.getRange(`${self.getColumnNameFromNumber(STcolumnNumber+20)}5`).values = [["FH-OPTIMIZED"]];
        const FHoptimazedDataRange = newSheet.getRange(`${self.getColumnNameFromNumber(STcolumnNumber+20)}5:${self.getColumnNameFromNumber(STcolumnNumber+19+20)}5`);
        FHoptimazedDataRange.merge();
        
        newSheet.getRange(`${self.getColumnNameFromNumber(STcolumnNumber+40)}5`).values = [["FC-OPTIMIZED"]];
        const FCoptimazedDataRange = newSheet.getRange(`${self.getColumnNameFromNumber(STcolumnNumber+40)}5:${self.getColumnNameFromNumber(STcolumnNumber+19+40)}5`);
        FCoptimazedDataRange.merge();
    }
};

// Add values to the columns for each threshold and interval derivated columns (CALs, FH and FC)
const addValuesThresholdIntervalColumns = async function(context, newSheet, optimized=false, self) {
    const usedRange = newSheet.getUsedRange();
    usedRange.load("values");
    await context.sync();
    const header = usedRange.getRow(5);
    usedRange.load("rowCount");
    header.load("values");
    await context.sync();
    const firstColumnRange = header.values[0].indexOf("SAMPLE\nTHRESHOLD");
    const qttThresholdAndInterval = optimized ? 12 : 4;
    for(let i=7; i<=usedRange.rowCount; i++) {
        const range = newSheet.getRange(`${self.getColumnNameFromNumber(firstColumnRange)}${i}:${self.getColumnNameFromNumber(firstColumnRange+((5*qttThresholdAndInterval)-1))}${i}`);
        range.load("values");
        await context.sync();
        let newValues = [];
        for(let j=0; j<qttThresholdAndInterval; j++) {
            const row = [];
            row.push(range.values[0][j*5]);
            const itemValues = ["", "", "", ""];
            if(range.values[0][j*5].length !== 0) {
                if(range.values[0][j*5].match(/YE|MO|DY/)) {
                    const calVarious = range.values[0][j*5].split("\n").filter(x => x.match(/YE|MO|DY/))[0];
                    itemValues[0] = calVarious; // Can be more than 1 option in time mesure (YE, MO, DY or HR)
                    if(calVarious.includes("YE")) {
                        itemValues[1] = String(Math.round(Number(calVarious.split(" ")[0]) * 365.25));
                    } else if(calVarious.includes("MO")) {
                        itemValues[1] = String(Math.round(Number(calVarious.split(" ")[0]) * 365.25/12));
                    } else {
                        itemValues[1] = calVarious.split(" ")[0];
                    }
                }
                if(range.values[0][j*5].match(/FH/)) {
                    const fhValue = range.values[0][j*5].split("\n").filter(x => x.match(/FH/))[0].split(" ")[0];
                    itemValues[2] = fhValue;
                }
                if(range.values[0][j*5].match(/FC/)) {
                    const fcValue = range.values[0][j*5].split("\n").filter(x => x.match(/FC/))[0].split(" ")[0];
                    itemValues[3] = fcValue;
                }
            }
            row.push(...itemValues);
            newValues = [...newValues, ...row];
            }
        range.values = [newValues];
    }
};

