// Excel form generator to change month from number to word
const monthNumberToWord = function(numberCell) {
    return `IF(${numberCell}="01","Jan",IF(${numberCell}="02","Feb",IF(${numberCell}="03","Mar",IF(${numberCell}="04","Apr",IF(${numberCell}="05","May",IF(${numberCell}="06","Jun",IF(${numberCell}="07","Jul",IF(${numberCell}="08","Aug",IF(${numberCell}="09","Sep",IF(${numberCell}="10","Oct",IF(${numberCell}="11","Nov","Dec")))))))))))`;
}

